@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap');

:root {
    --lite-font: #fff;
    --dark-red: rgb(34, 24, 24);
    --poppins: 'Poppins', sans-serif;
}

/* Only Mobile */
html, body {
    position: relative;
    margin: 0 auto;
    color: var(--lite-font);
    font-family: var(--poppins);
    max-width: 700px;
    min-height: 100vh;
    overflow: hidden;
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
}

body {
    background: url('./assets/bg.jpg') no-repeat center center;
    background-size: cover;
} 

/* Game Setup Styles */
.setup-container form {
    padding: 2em;
}

.setup-container form input {
    background: #fff;
    opacity: .8;
    border: 2px solid #dddddd;
    border-radius: 20mm;
    font-size: 1.2rem;
    padding: .8rem 2rem;
    box-shadow: 0 0 3px 1px;
}

.setup-container form input:focus {
    outline: none;
}

.setup-container section {
    width: 96vw;
    margin: 0 auto;
    margin-top: -1em;
    height: 420px;
}

.setup-container h2 {
    font-size: 1.6rem;
    font-weight: 900;
    color: black;
}

.setup-container ul {
    list-style: none;
    overflow-y: scroll;
}

.setup-container li {
    cursor: pointer;
    padding: 3px 0 0 1em;
    margin: 2px 0;
    width: 200px;
    font-size: 1.2rem;
    font-weight: 900;
    background: var(--dark-red);
}

.setup-container section ul h2 {
    width: 200px;
    color: var(--lite-font);
    text-align: end;
    text-shadow: 1px 1px 2px var(--dark-red);
}

/* In Game Styles */
.ingame-container {
    height: 100vh;
    overflow-y: scroll;
}

.card-container {
    position: relative;
    width: 96vw;
    margin: .6em auto;
    background: var(--dark-red);
    opacity: .8;
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(250px, 1fr) );
}

.card-container h3 {
    position: absolute;
    top: 10px;
    left: 10px;
}

.health-container {
    display: grid;
    width: 70vw;
    margin: 0 auto;
    grid-template-columns:  1fr 1fr 1fr;
    place-items: center;
    font-size: 2.6rem;
    font-weight: 900;
    padding: .2em .6em;
}

.minus, .plus, .health {
    display: grid;
    place-content: center;
    padding: 0 .6em;
    width: 50px;
}

h4 {
    text-align: end;
    margin-right: 10px;
    text-shadow: 1px 1px 2px var(--dark-red);
}

.dead {
    background: black;
    opacity: .9;
    color: black;
}